html,
body {/* fonts */
    height: 100%;
    line-height: 1.8;
    letter-spacing: .5px;
    background: white;
    margin: 0;
}

/* loading-container */
.loading-container {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background-color: white;
    display: block;
    .loading-image {
        position: fixed;
        top: 50%;
        left: 50%;
        height: 500px;
        width: 500px;
        transform: translate(-50%, -50%);
        background: url("./loader.gif") no-repeat center center; 
        -webkit-background-size: cover;
        -moz-background-size: cover;
        -o-background-size: cover;
        background-size: 100%;
        z-index: 5;
    }
}